@import url(https://fonts.googleapis.com/css?family=Anton|Lilita+One|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#myFooter {
    background-color: #182c39;
    color: white;
    padding-top: 20px;
}

#map-container {
    height: 240px;
    width: 100%;
    margin-top: 30px;
    margin-bottom: 10px;
}

#myFooter .row {
    margin: 0 85px;
}

#myFooter .footer-copyright {
    background-color: #10222e;
    padding-top: 3px;
    padding-bottom: 3px;
    text-align: center;
}

#myFooter .footer-copyright p {
    margin: 10px;
    color: #ccc;
}

#myFooter .container {
    width: auto;
}

#myFooter ul {
    list-style-type: none;
    padding-left: 0;
    line-height: 1.7;
}

#myFooter h5 {
    font-size: 18px;
    color: white;
    font-weight: bold;
    margin-top: 30px;
}

#myFooter a {
    color: #d2d1d1;
    text-decoration: none;
}

#myFooter a:hover,
#myFooter a:focus {
    text-decoration: none;
    color: white;
}

#myFooter .social-networks {
    text-align: center;
    padding-top: 30px;
    padding-bottom: 38px;
}

#myFooter .fa {
    font-size: 30px;
    margin-right: 15px;
    margin-left: 20px;
    background-color: white;
    color: #182c39;
    border-radius: 51%;
    padding: 10px;
    height: 50px;
    width: 50px;
    text-align: center;
    line-height: 31px;
    text-decoration: none;
    -webkit-transition: color 0.2s;
    transition: color 0.2s;
}

#myFooter .fa-facebook:hover {
    color: #2b55ff;
}

#myFooter .fa-facebook:focus {
    color: #2b55ff;
}

#myFooter .fa-google-plus:hover {
    color: red;
}

#myFooter .fa-google-plus:focus {
    color: red;
}

#myFooter .fa-twitter:hover {
    color: #00aced;
}

#myFooter .fa-twitter:focus {
    color: #00aced;
}

@media screen and (max-width: 767px) {
    #myFooter {
        text-align: center;
    }
    #myFooter .row {
        margin: 0;
    }
}



/* CSS used for positioning the footers at the bottom of the page. */
/* You can remove this. */

html{
    height: 100%;
}

body{
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    height: 100%;
}

.content{
   flex: 1 0 auto;
   -webkit-flex: 1 0 auto;
   min-height: 200px;
}

#myFooter{
   flex: 0 0 auto;
   -webkit-flex: 0 0 auto;
}
.layout {
  background-clip: border-box;
  background-position-x: 50%;
  background-position-y: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
  background-attachment: fixed;
  width: 100%;
  height: 720px;
  margin: 0%;
  padding: 0%;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  display: table;
  position: relative;
  -webkit-hyphens: none;
  -ms-hyphens: none;
      hyphens: none;
}

canvas {
  display: block;
  background: #200122;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right,
      #6f0000,
      #200122);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear,
      left top, right top,
      from(#6f0000),
      to(#200122));
  background: -webkit-linear-gradient(left,
      #6f0000,
      #200122);
  background: linear-gradient(to right,
      #6f0000,
      #200122);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  position: fixed;
  z-index: -1;
}

.navbar-home {
  background: rgba(0, 0, 0, 0.9);
}

.layout .brand-name {
  font-family: "Anton", sans-serif;
  font-size: 80px;
  text-align: center;
  vertical-align: middle;
  margin-top: 250px;
  position: relative;
  color: white;
  opacity: 0.6;
  letter-spacing: 10px;
}

.layout .work {
  text-align: center;
  vertical-align: middle;
  font-family: "Anton", sans-serif;
  font-size: 30px;
  color: white;
  opacity: 0.5;
  letter-spacing: 4px;
}

.about-section {
  background-color: #1f1e23;
  font-family: "Lilita One", cursive;
  font-size: 2vw;
  text-align: center;
  padding: 4em;
}

.about-section h2 {
