}

.about-section hr {
  border: 0.13vw solid yellow;
  width: 30%;
}

.about-section .about-content p {
  font-family: "Roboto", sans-serif;
  text-align: center;
  color: darkgrey;
  vertical-align: middle;
  padding-top: 4em;
  font-size: 20px;
  margin: 0.5em;
  margin-bottom: 0em;
}

.about-section .about-content img {
  border-radius: 50%;
